import React, {useState} from 'react'
import { dbService } from '../../../tools/fbase'
import {useHistory} from 'react-router-dom'
import Select from 'react-select'
import {typeSelect} from '../../../tools/types'
import makeAnimated from 'react-select/animated';
import Picker, { SKIN_TONE_MEDIUM_DARK } from "emoji-picker-react";
import './MakeGallery.css'

const animatedComponents = makeAnimated();

const MakeGallery = ({userObj}) => {
    const history = useHistory();
    const [galleryName, setGalleryName] = useState("");
    const [typess, setTypess] = useState([]);
    const [type, setType] = useState("");
    const [color, setColor] = useState("#f0f0f0");
    const [desc, setDesc] = useState("");
    const [chosenEmoji, setChosenEmoji] = useState(null);
    const [showemoji, setShowemoji] = useState("none");

    const onEmojiClick = (event, emojiObject) => {
        setChosenEmoji(emojiObject);
        setGalleryName(emojiObject.emoji + galleryName);
      };

    const showEmojiSelect = () => {
        if(showemoji === 'none') {setShowemoji('flex')}
        else{setShowemoji('none')}
    }

    
    const submitGallery = async e => {
        e.preventDefault();
        console.log(galleryName, color, desc, userObj.uid);
        const galleryOne = {
            galleryName,
            color,
            desc,
            userId:userObj.uid,
            displayName:userObj.displayName,
            like_num:0,
            comment_num:0,
            collection_num:0,
            typess:typess,
            left_color:'white',
            right_color:'white'
        };

        await dbService.collection("users").add(galleryOne);
        alert(" !");
        history.go(0);
    }

    const changeTypes = e => {
        let tts = [];
        e.forEach(i => {
            tts.push(i.label)
        });

        setTypess(tts)
    }

    return (
        <div className="uploadcontainer">
            <div className="uploadIncontainer">
                <form onSubmit={submitGallery}>
                    <p className="thanks">   </p>
                    <p className="add-thanks">     </p>
                    <p className="inputLabel"> </p>
                    <p className="inputLabel">ex.  </p>

                    <div style={{display:`${showemoji}`}}>
                        <Picker
                            onEmojiClick={onEmojiClick}
                            disableAutoFocus={true}
                            groupNames={{ smileys_people: "PEOPLE" }}
                            native
                        />
                    </div>
                    <span onClick={showEmojiSelect} className="emojiSelect" > </span>
                    <input autoSize={{ minRows: 1, maxRows: 2 }} placeholder=" " 
                        className="nameInput" 
                        value={galleryName} 
                        onChange={(e) => {setGalleryName(e.currentTarget.value)}}>
                    </input>

                    <p className="inputLabel">    </p>
                    <textarea 
                        className="nameInput" 
                        value={desc} 
                        onChange={(e) => {setDesc(e.currentTarget.value)}}
                        autoSize={{ minRows: 3, maxRows: 5 }}
                        placeholder=" "
                    ></textarea>
                    <div style={{marginTop:'10%'}}>
                    <p className="inputLabel" style={{fontSize:"15px"}}>  </p>
                    <Select
                        closeMenuOnSelect={false}
                        components={animatedComponents}
                        options={typeSelect} 
                        onChange={changeTypes}
                        isMulti
                    />
                        {/* <Select options={types} onChange={e => {setType(e.label); console.log(e.label)}} /> */}
                    </div>
                    <div style={{marginTop:'10%'}}>
                        <p className="inputLabel" style={{fontSize:"15px"}}>    </p>
                        <input type="color" value={color} onChange={e => setColor(e.currentTarget.value)}/>
                    </div>
                    <div>
                    <button className="inputButton" onClick={submitGallery}></button>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default MakeGallery
